import React, { useContext } from 'react'
import { Helmet } from 'react-helmet'

import AppContext from '@src-contexts/app-context'

import { AllSiteMetadataQuery } from '@src-queries'

const SEO = () => {
	const pageValues = useContext(AppContext)

	const allSiteMetadata = AllSiteMetadataQuery()

	const metaTitle = pageValues?.title
		? `${pageValues.title} | ${allSiteMetadata?.title}`
		: allSiteMetadata?.title
	const metaDescription =
		pageValues?.description || allSiteMetadata?.description
	const metaLang = pageValues?.lang ? pageValues.lang : allSiteMetadata?.lang
	const metaCanonical = pageValues?.canonical
		? `${allSiteMetadata?.siteUrl}${pageValues?.canonical}`
		: null
	const metaUrl = pageValues?.location
		? `${allSiteMetadata?.siteUrl}${pageValues?.location}`
		: allSiteMetadata?.siteUrl
	const metaImage = pageValues?.image || allSiteMetadata?.image

	return (
		<Helmet
			htmlAttributes={{
				lang: metaLang,
			}}
			title={metaTitle}
			link={metaCanonical ? [{ rel: 'canonical', href: metaCanonical }] : []}
			meta={[
				{
					name: `robots`,
					content: 'noindex,follow',
				},
				{
					name: `googlebot`,
					content: 'noindex,follow',
				},
				{
					name: `description`,
					content: metaDescription,
				},
				{
					property: `og:site_name`,
					content: allSiteMetadata?.title,
				},
				{
					property: `og:title`,
					content: metaTitle,
				},
				{
					property: `og:description`,
					content: metaDescription,
				},
				{
					property: `og:url`,
					content: metaUrl,
				},
				{
					property: `og:type`,
					content: `website`,
				},
				{
					name: `twitter:card`,
					content: `summary_large_image`,
				},
				{
					name: `twitter:title`,
					content: metaTitle,
				},
				{
					name: `twitter:description`,
					content: metaDescription,
				},
				{
					name: `twitter:image`,
					content: metaImage,
				},
			]}
		/>
	)
}

export default SEO
