import React, { useContext } from 'react'
import { css } from '@emotion/core'
import { Link } from 'gatsby'

import AppContext from '@src-contexts/app-context'

import Wrapper from '@src-components/common/wrapper'

import FAIcon from '@src-components/block/fa-icon'

const BreadcrumbList = () => {
	const pageValues = useContext(AppContext)

	const breadcrumbListItems = pageValues.breadcrumbItems.map(
		breadcrumbListItem => {
			return (
				<li
					className="flex items-center whitespace-no-wrap"
					css={css`
						&:not(:first-of-type):before {
							--bg-opacity: 1;
							border-color: rgba(107, 114, 128, var(--bg-opacity));
							border-style: solid;
							border-width: 1px 1px 0 0;
							content: '';
							display: inline-block;
							height: 0.45em;
							margin: 0.45em 0.45em 0.15em 0.15em;
							transform: rotate(45deg);
							width: 0.45em;
						}
					`}
					key={breadcrumbListItem.url}
				>
					<Link
						to={breadcrumbListItem.url}
						className="flex items-center whitespace-no-wrap hover:text-gray-900"
					>
						<FAIcon icon={breadcrumbListItem.icon} size="xs" className="mr-1" />
						{breadcrumbListItem.label}
					</Link>
				</li>
			)
		}
	)

	return (
		<Wrapper>
			<ol className="flex items-center overflow-x-scroll text-gray-700 text-sm font-medium py-2">
				{breadcrumbListItems}
			</ol>
		</Wrapper>
	)
}

export default BreadcrumbList
