import React, { useContext } from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-scroll'
import classnames from 'classnames'

import AppContext from '@src-contexts/app-context'

const HeadingList = props => {
	const { items, isHierarchy } = props

	const pageValues = useContext(AppContext)

	return (
		<ol className={isHierarchy ? 'ml-2' : null}>
			{items.map(item => {
				const classNames = classnames(
					'block w-full rounded-md outline-none text-sm text-gray-500 px-2 py-1 font-semibold hover:text-gray-700 hover:bg-gray-200 focus:bg-gray-200 focus:text-gray-700 transition duration-150 ease-in-out break-all cursor-pointer',
					pageValues?.activeTOC.current === item.url.replace('#', '')
						? 'bg-gray-100'
						: ''
				)

				return (
					<li key={item.url}>
						<Link
							data-id={item.url.replace('#', '')}
							to={item.url.replace('#', '')}
							smooth={true}
							offset={-128}
							duration={800}
							className={classNames}
							onClick={() => pageValues.setIsOpenTOC(false)}
						>
							{item.title}
						</Link>
						{item.items && (
							<HeadingList items={item.items} isHierarchy={true} />
						)}
					</li>
				)
			})}
		</ol>
	)
}

HeadingList.propTypes = {
	items: PropTypes.array.isRequired,
	isHierarchy: PropTypes.bool,
	setIsSlideOpen: PropTypes.func,
}

export default HeadingList
