import React from 'react'
import PropTypes from 'prop-types'

import SimpleLayout from './simple'
import OneColumnLayout from './one-column'
import TwoColumnLayout from './two-column'

const Layout = props => {
	const { pattern, children } = props

	switch (pattern) {
		case 'simple':
			return <SimpleLayout>{children}</SimpleLayout>
		case 'one-column':
			return <OneColumnLayout>{children}</OneColumnLayout>
		case 'two-column':
		default:
			return <TwoColumnLayout>{children}</TwoColumnLayout>
	}
}

Layout.propTypes = {
	pattern: PropTypes.string,
	children: PropTypes.node.isRequired,
}

export default Layout
