import React, { useContext } from 'react'
import { Link } from 'gatsby'

import { faBook, faSearch, faBars } from '@fortawesome/free-solid-svg-icons'

import AppContext from '@src-contexts/app-context'

import { AllSiteMetadataQuery } from '@src-queries'

import Wrapper from '@src-components/common/wrapper'

import GlobalNav from '@src-components/parts/global-nav'
import GlobalSubNav from '@src-components/parts/global-sub-nav'

import FAIcon from '@src-components/block/fa-icon'
import ScreenReaderText from '@src-components/block/screen-reader-text'

import Logo from '@static/logo.svg'

const Header = props => {
	const siteMetadata = AllSiteMetadataQuery()

	const pageValues = useContext(AppContext)

	return (
		<header className="font-sans w-full bg-white shadow sticky print:static top-0 z-40">
			<Wrapper>
				<div className="flex flex-row justify-between">
					<h1 className="self-start my-4">
						<Link
							to={siteMetadata.pathPrefix}
							className="inline-block w-32 sm:w-48"
						>
							<Logo />
							<ScreenReaderText>{siteMetadata.title}</ScreenReaderText>
						</Link>
					</h1>
					<div className="flex flex-col justify-center md:justify-between">
						<div className="hidden md:block self-end">
							<GlobalSubNav />
						</div>
						<div className="self-end">
							<nav className="md:hidden">
								<ul className="flex">
									{pageValues?.tocItems && (
										<li className="mr-1">
											<button
												className="w-16 h-16 rounded-md outline-none bg-gray-100 text-gray-600 px-2 py-1 font-semibold text-xs leading-relaxed hover:text-gray-700 hover:bg-gray-200 focus:outline-none focus:bg-gray-200 focus:text-gray-700 transition duration-150 ease-in-out"
												onClick={() => {
													pageValues?.setIsOpenTOC(true)
												}}
											>
												<FAIcon icon={faBook} />
												<span className="block">目次</span>
											</button>
										</li>
									)}
									<li className="mr-1">
										<button
											className="w-16 h-16 rounded-md outline-none bg-gray-100 text-gray-600 px-2 py-1 font-semibold text-xs leading-relaxed hover:text-gray-800 hover:bg-gray-200 focus:outline-none focus:bg-gray-200 focus:text-gray-800 transition duration-150 ease-in-out"
											onClick={() => {
												pageValues?.setIsOpenSearch(true)
											}}
										>
											<FAIcon icon={faSearch} />
											<span className="block">記事検索</span>
										</button>
									</li>
									<li>
										<button
											className="w-16 h-16 rounded-md outline-none text-gray-700 px-2 py-1 font-semibold text-xs leading-relaxed hover:text-gray-800 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 focus:text-gray-800 transition duration-150 ease-in-out"
											onClick={() => {
												pageValues?.setIsOpenMenu(true)
											}}
										>
											<FAIcon icon={faBars} />
											<span className="block">メニュー</span>
										</button>
									</li>
								</ul>
							</nav>
							<GlobalNav />
						</div>
					</div>
				</div>
			</Wrapper>
		</header>
	)
}

export default Header
