import React from 'react'
import { Link } from 'gatsby'
import classnames from 'classnames'

import FAIcon from '@src-components/block/fa-icon'

import menuConfig from '@config/menu'

const DrawerNav = () => {
	return (
		<>
			<h3 className="inline-block font-medium text-gray-600 border-b-2 border-gray-600">
				コンテンツ
			</h3>
			<nav className="my-4">
				<ul>
					{menuConfig.drawerNavItems.map(drawerNavItem => {
						return (
							<li key={drawerNavItem.url} className="mb-2">
								<Link
									to={drawerNavItem.url}
									className={classnames(
										'flex items-center p-2 rounded-md outline-none font-medium hover:bg-gray-100 focus:bg-gray-100 text-gray-700 hover:text-gray-900 focus:text-gray-900 transition duration-150 ease-in-out',
										drawerNavItem.url === location.pathname
											? 'bg-gray-100'
											: null
									)}
								>
									<FAIcon
										icon={drawerNavItem.icon}
										size="lg"
										className="mr-2"
									/>
									{drawerNavItem.label}
									{drawerNavItem.badge && (
										<span className="flex items-center justify-center text-sm bg-gray-200 text-gray-600 font-semibold h-6 px-2 rounded-full ml-auto">
											{drawerNavItem.badge}
										</span>
									)}
								</Link>
							</li>
						)
					})}
				</ul>
			</nav>
		</>
	)
}

export default DrawerNav
