import React from 'react'
import { css } from '@emotion/core'
import { Link } from 'gatsby'

import FAIcon from '@src-components/block/fa-icon'

import menuConfig from '@config/menu'

const GlobalNav = () => {
	return (
		<nav className="hidden md:block">
			<ul className="flex">
				{menuConfig.globalNavItems.map(globalNavItem => {
					return (
						<li key={globalNavItem.label}>
							<Link
								to={globalNavItem.url}
								className="inline-block py-2 px-4 text-gray-600 font-medium hover:text-gray-900 focus:text-gray-900 border-white border-b-2 transition duration-150 ease-in-out"
								css={css`
									&[aria-current],
									&:hover,
									&:focus {
										border-color: #161e2e;
										border-color: rgba(22, 30, 46, var(--border-opacity));
										color: #161e2e;
										color: rgba(22, 30, 46, var(--text-opacity));
										--text-opacity: 1;
										--border-opacity: 1;
									}
								`}
							>
								<FAIcon icon={globalNavItem.icon} className="mr-1" />
								{globalNavItem.label}
							</Link>
						</li>
					)
				})}
			</ul>
		</nav>
	)
}

export default GlobalNav
