import React from 'react'
import { Link } from 'gatsby'

import { faBullhorn } from '@fortawesome/free-solid-svg-icons'

import FAIcon from '@src-components/block/fa-icon'

import newsConfig from '@config/news'

import Wrapper from '@src-components/common/wrapper'

const News = () => {
	if (!newsConfig.message) return null

	return (
		<div className="w-full border-b">
			<Wrapper>
				<div className="py-1 sm:flex items-center">
					<span className="px-1 py-1 sm:py-0 text-gray-900 text-sm font-semibold bg-gray-200 rounded sm:mr-2 whitespace-no-wrap">
						<FAIcon icon={faBullhorn} className="mr-1" />
						お知らせ
					</span>
					<div className="font-semibold text-xs leading-4 mt-1 sm:mt-0">
						{newsConfig.url ? (
							<Link to={newsConfig.url}>{newsConfig.message}</Link>
						) : (
							newsConfig.message
						)}
					</div>
				</div>
			</Wrapper>
		</div>
	)
}

export default News
