import React, { useContext, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { css } from '@emotion/core'

import { faBook, faSearch, faBars } from '@fortawesome/free-solid-svg-icons'

import AppContext from '@src-contexts/app-context'

import Header from '@src-components/parts/header'
import News from '@src-components/parts/news'
import BreadcrumbList from '@src-components/parts/breadcrumb-list'
import Footer from '@src-components/parts/footer'

import Sidebar from '@src-components/parts/sidebar'
import WidgetArea from '@src-components/parts/widget-area'
import TOC from '@src-components/parts/toc'

import SlideContents, {
	SlideContentsHeader,
	SlideContentsContent,
} from '@src-components/parts/slide-contents'
import SearchForm from '@src-components/parts/search/form'
import SearchResult from '@src-components/parts/search/result'

import DrawerNav from '@src-components/parts/drawer-nav'

const TwoColumnLayout = props => {
	const { children } = props

	const pageValues = useContext(AppContext)

	useEffect(() => {
		pageValues?.isOpenMenu || pageValues?.isOpenTOC || pageValues?.isOpenSearch
			? document.body.classList.add('overflow-hidden')
			: document.body.classList.remove('overflow-hidden')
	}, [pageValues?.isOpenMenu, pageValues?.isOpenTOC, pageValues?.isOpenSearch])

	useEffect(() => {
		if (pageValues?.isOpenTOC) {
			const tocElements = document.querySelectorAll('.article-toc')
			for (const tocElement of tocElements) {
				const linkElements = tocElement.querySelectorAll('a')
				for (const linkElement of linkElements) {
					if (
						linkElement.getAttribute('data-id') === tocElement.dataset.active
					) {
						pageValues.activeTOC.current = tocElement.dataset.active
						return
					}
				}
			}
		}
	}, [pageValues?.isOpenTOC])

	const [searchValue, setSearchValue] = useState('')
	const [searchResultData, setSearchResultData] = useState([])

	return (
		<div className="flex flex-col min-h-screen tracking-wide leading-6">
			<Header />
			<News />
			<BreadcrumbList />
			<main className="flex-grow py-8">
				<div className="container mx-auto px-4 sm:flex sm:flex-row sm:flex-no-wrap">
					<article className="w-full md:w-2/3 break-all">{children}</article>
					<Sidebar>
						{pageValues?.tocItems && (
							<WidgetArea title="目次" icon={faBook}>
								<div
									className="overflow-y-scroll"
									css={css`
										max-height: 25vh;
										min-height: 250px;
									`}
								>
									<TOC items={pageValues?.tocItems} />
								</div>
							</WidgetArea>
						)}
					</Sidebar>
				</div>
			</main>
			<Footer />
			<SlideContents isOpen={pageValues?.isOpenMenu}>
				<SlideContentsHeader
					title="メニュー"
					icon={faBars}
					close={pageValues?.setIsOpenMenu}
				></SlideContentsHeader>
				<SlideContentsContent>
					<DrawerNav />
				</SlideContentsContent>
			</SlideContents>
			<SlideContents isOpen={pageValues?.isOpenSearch}>
				<SlideContentsHeader
					title="記事検索"
					icon={faSearch}
					close={pageValues?.setIsOpenSearch}
				>
					<SearchForm
						value={searchValue}
						onChange={event => setSearchValue(event.target.value)}
					/>
				</SlideContentsHeader>
				<SlideContentsContent>
					<SearchResult
						value={searchValue}
						searchResultData={searchResultData}
						setSearchResultData={setSearchResultData}
					/>
				</SlideContentsContent>
			</SlideContents>
			<SlideContents isOpen={pageValues?.isOpenTOC}>
				<SlideContentsHeader
					title="目次"
					icon={faBook}
					close={pageValues?.setIsOpenTOC}
				></SlideContentsHeader>
				<SlideContentsContent>
					<TOC items={pageValues?.tocItems} />
				</SlideContentsContent>
			</SlideContents>
		</div>
	)
}

TwoColumnLayout.propTypes = {
	children: PropTypes.node.isRequired,
}

export default TwoColumnLayout
